.row{
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  height: 100vh;
}
.column{
  flex:50%;
  
}
header{
  margin-top: 250px;
}
form{
  margin-top: 120px;
}
.left{
  background: rgb(211, 142, 142);
  background-image: url("./cloud.jpg");
  
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.right{
  background: #2c3e50;
}
h1{
  font-size:50px;
}
p{
  font-size:20px;
}
input {
  background-color: transparent;
  border: 0;
  border-bottom: solid 3px #fff;
  width: 25%;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  font-size: 20px;
  margin-right: 10px;
  margin-bottom: 50px;
}
button{
  border: 0;
  padding: 8px 20px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  color: rgb(0, 0, 0);
  font-weight: 100;
  transition: 0.5s;
}
button:hover{
  background: black;
  color:white;
}


@media(max-width:500px){
  header{
    margin-top:100px;
    margin-bottom: 100px;
  }

}